
#film-map-body {
	background-color: #e5e5e5;
	margin: 0;
}
#top-ban {
	height: 50px;
	background-color: #2c3e50;
	color: whitesmoke;
}
#top-ban-label {
	font-size: 2em;
	float: left;
	margin-left: 10px;
	margin-top: 7px;
}
#top-ban-sub {
	padding-top: 20px;
	padding-left: 20px;
}
.search-field {
	padding-left: 10px;
	padding-right: 10px;
}
#film-info-search {
	width: 26%;
	float: left;
	min-height: calc(100vh - 50px);
	background-color: #3f5c72;
	color: #eeeeee;
}
#film-detail {
	margin-top: 20px;
}
#film-release-year {
	margin-top: 2px;
}
#film-detail .film-item {
	width: 90%;
	padding-top: 10px;
	margin-left: 15px;
	clear: both;
}
#film-detail .item-label {
	float: left;
}
#film-detail .item-value {
	float: left;
	color: #ff9632;
}
.font-small {
	font-size: 15px;
}
#film-title {
	font-size: 18px;
	float: left;
	color: #f0f0f0;
	font-weight: 600;
}
#film-release-year {
	margin-left: 10px;
	color: #50799b;
}
#film-fun-facts {
	padding-top: 30px;
}
#main {
	font-family: 'Avenir', Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	color: #2c3e50;
}
#map-detail {
	width: 74%;
	height: 100%;
	float: right;
	background-color: #AAB2BE;
}
input {
	width: 100%;
	font-size: 14px;
	color: #2c3e50;
	box-shadow: inset 0 1px 4px rgba(0, 0, 0, .4);
	-webkit-transition: border-color ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
	-webkit-transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
	transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
	font-weight: 300;
	padding: 10px 20px;
	border: none;
	border-radius: 20px;
	letter-spacing: 1px;
	box-sizing: border-box;
	font-family: "Helvetica Neue", Helvetica, sans-serif;
}
input:focus {
	border-color: #8ad2ff;
	outline: 0;
	box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px #8ad2ff;
}
.fa-times {
	cursor: pointer;
}
i.fa {
	float: right;
	position: relative;
	top: 27px;
	right: 20px;
}
ul {
	width: 24%;
	position: absolute;
	padding: 0;
	margin-top: 8px;
	margin-left: 20px;
	min-width: 15%;
	background-color: #fff;
	list-style: none;
	border-radius: 4px;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
	z-index: 1000;
}
li {
	padding: 10px 16px;
	border-bottom: 1px solid #ccc;
	cursor: pointer;
}
li:first-child {
	border-top-left-radius: 4px;
	border-top-right-radius: 4px;
}
li:last-child {
	border-bottom-left-radius: 4px;
	border-bottom-right-radius: 4px;
	border-bottom: 0;
}
span {
	display: block;
	color: #2c3e50;
}
.active {
	background-color: #8ad2ff;
}
.active span {
	color: white;
}




